import React, { Component, useEffect, useState } from 'react';
import axios from 'axios';
import DataTable from 'react-data-table-component';

const columns = [
    {
        name: 'Event Time',
        selector: 'time',
        sortable: true,
        right: true,
    },
  {
    name: 'Member System Name',
    selector: 'memberSystemName',
    sortable: true,
  },
  {
    name: 'FDE Name',
    selector: 'fdeName',
    sortable: true,
    right: true,
  },
  
  {
    name: 'Failure Name',
    selector: 'failureName',
    sortable: true,
    right: true,
  
  },
  {
    name: 'ATA Chapter',
    selector: 'ataChapter',
    sortable: true,
    right: true,
  
  },
  {
    name: 'Flight Leg',
    selector: 'flightLeg',
    sortable: true,
    right: true,
  
  },
  {
    name: 'Flight Phase',
    selector: 'flightPhase',
    sortable: true,
    right: true,
  
  }
  
];
class FDECorrelationEvent extends React.Component{

    constructor(props) {
        super(props);
        this.state = {
          error: null,
          isLoaded: false,
          items: []
        };
      }
    
    componentDidMount() {
        axios.get('http://localhost:4000/fdeCorrelation')
        .then(response => {
            console.log(response.data);
            this.setState({ items: response.data });
        })
        .catch(function (error){
            console.log(error);
        })
    }

    render() {
        return (
         
            <DataTable
                title="FDE Correlation Events"
                columns={columns}
                data={this.state.items.event}
                fixedHeader
                fixedHeaderScrollHeight="300px"
                highlightOnHover
                dense
            />
        )
      }
    };

export default FDECorrelationEvent;