import React, { Component, useEffect, useState } from 'react';
import axios from 'axios';
import DataTable from 'react-data-table-component';

const columns = [
    {
        name: 'Event Time',
        selector: 'time',
        sortable: true,
        right: true,
    },
    {
        name: 'Event Type',
        selector: 'type',
        sortable: true,
        right: true,
    },
    {
        name: 'Member System Name',
        selector: 'memberSystemName',
        sortable: true,
        right: true,
    },
    {
        name: 'Failure Name',
        selector: 'failureName',
        sortable: true,
        right: true,
    },
    {
        name: 'Paramter Name',
        selector: 'name',
        sortable: true,
        right: true,
    },
  
  
];
class Test extends React.Component{

    constructor(props) {
        super(props);
        this.state = {
          error: null,
          isLoaded: false,
          fde: [],
          fdeCorrelation: [],
          failure: [],
          acmf: []
        };
      }
    
    componentDidMount() {
        axios.get('http://localhost:4000/failureevent/')
        .then(response => {
            console.log(response.data);
            this.setState({ failure: response.data });
        })
        .catch(function (error){
            console.log(error);
        });

        axios.get('http://localhost:4000/fde/')
        .then(response => {
            console.log(response.data);
            this.setState({ fde: response.data });
        })
        .catch(function (error){
            console.log(error);
        });

        axios.get('http://localhost:4000/fdeCorrelation/')
        .then(response => {
            console.log(response.data);
            this.setState({ fdeCorrelation: response.data });
        })
        .catch(function (error){
            console.log(error);
        });

        axios.get('http://localhost:4000/acmfevent/')
        .then(response => {
            console.log(response.data);
            this.setState({ acmf: response.data });
        })
        .catch(function (error){
            console.log(error);
        });
    }

    render() {

        var data = [];
        for(var e in this.state.fde.event){
            data.push(this.state.fde.event[e]);
        }

        for(var e in this.state.failure.event){
            data.push(this.state.failure.event[e]);
        }

        for(var e in this.state.fdeCorrelation.event){
            data.push(this.state.fdeCorrelation.event[e]);
        }

        for(var e in this.state.acmf.event){
            data.push(this.state.acmf.event[e]);
        }
        
       
        console.log(data);
        
        return (
         
            <DataTable
                title="Recent Events"
                columns={columns}
                data={data}
                fixedHeader
                fixedHeaderScrollHeight="500px"
                highlightOnHover
                dense
                overflowY
            />
        )
      }
    };

export default Test;