import React, { Component } from 'react';
import {Scatter} from 'react-chartjs-2';


const state = {
    
    labels: ['GPM R1 (10888)', 'IHDC (90)', 'HLRM A on IMC (189)'],
    datasets: [{
      
        label: 'Scatter Dataset',
        data: [{
            x: -10,
           
        }, {
            x: 0,
            
        }, {
            x: 10,
           
        }]
    }]
    
  }
class EventsGraph extends React.Component{

    constructor(props) {
        super(props);
        this.state = {

        }
    }

    render(){
        return(
            <div>
        <Scatter 
          data={state}
          options={{
            title:{
              display:true,
              text:'FDE Correlation Timeline',
              fontSize:20
            },
            legend:{
              display:true,
              position:'bottom'
            },
            scales: {      
              xAxes: [{     
                type: 'time',
                time: {
                  parser: 'HH:mm',
                  unit: 'hour',
                  stepSize: 1,
                  displayFormats: {
                    hour: 'HH:mm'   
                  },          
                  tooltipFormat: 'HH:mm'          
                },
                ticks: {
                  min: '00:00',
                  max: '24:00',
                  callback: (value, index) => index == 24 ? '24:00' : value
                }
              }],
              yAxes: [{
                ticks: {
                  beginAtZero: true,
                  stepSize: 5
                }
              }]
            }
          }}
        />
      </div>
        );
    }



}


export default EventsGraph;