import React, { Component } from 'react';
import {Bar} from 'react-chartjs-2';
import axios from 'axios';


const state = {
    
    labels: ['GPM R1 (10888)', 'IHDC (90)', 'HLRM A on IMC (189)'],
    datasets: [
      {
        label: 'Readings',
        fill: false,
        lineTension: 0.5,
        backgroundColor: 'rgba(75,192,192,1)',
        borderColor: 'rgba(0,0,0,1)',
        borderWidth: 2,
       
        data: [65, 59, 80]
      }
    ]
  }
class EventsGraph extends React.Component{

  constructor(props) {
    super(props);
    this.state = {
      error: null,
      isLoaded: false,
      fde: [],
      fdeCorrelation: [],
      failure: [],
      acmf: []
    };
  }

componentDidMount() {
    axios.get('http://localhost:4000/failureevent/')
    .then(response => {
        console.log(response.data);
        this.setState({ failure: response.data });
    })
    .catch(function (error){
        console.log(error);
    });

    axios.get('http://localhost:4000/fde/')
    .then(response => {
        console.log(response.data);
        this.setState({ fde: response.data });
    })
    .catch(function (error){
        console.log(error);
    });

    axios.get('http://localhost:4000/fdeCorrelation/')
    .then(response => {
        console.log(response.data);
        this.setState({ fdeCorrelation: response.data });
    })
    .catch(function (error){
        console.log(error);
    });

    axios.get('http://localhost:4000/acmfevent/')
    .then(response => {
        console.log(response.data);
        this.setState({ acmf: response.data });
    })
    .catch(function (error){
        console.log(error);
    });
}

    render(){

      var labels = [];

      for(var e in this.state.fdeCorrelation.event){
        labels.push(this.state.fdeCorrelation.event[e].memberSystemName);
      }
      for(var e in this.state.failure.event){
        labels.push(this.state.failure.event[e].memberSystemName);
      }
      for(var e in this.state.failure.event){
        labels.push(this.state.failure.event[e].memberSystem);
      }

   

    var uni = new Set(labels);
    uni = [...uni];

    console.log(uni);
   


       const cfg= {
    
        
         datasets: [
           {
             label: 'Readings',
             fill: false,
             lineTension: 0.5,
             backgroundColor: 'rgba(75,192,192,1)',
             borderColor: 'rgba(0,0,0,1)',
             borderWidth: 2,
           
             data: labels
           }
         ]
       }


        return(
            <div>
        <Bar 
          data={cfg}
          options={{
            parsing: {
              xAxisKey: uni,
              yAxisKey: labels
          },
            title:{
              display:true,
              text:'Event Count By Member System',
              fontSize:20
            },
            legend:{
              display:true,
              position:'bottom'
            }
          }}
        />
      </div>
        );
    }



}


export default EventsGraph;