import React, { Component, useEffect, useState } from 'react';
import axios from 'axios';
import DataTable from 'react-data-table-component';

const columns = [
    {
        name: 'Start Time',
        selector: 'time',
        sortable: true,
        right: true,
    },
  {
    name: 'Parameter Name',
    selector: 'name',
    sortable: true,
  },
  {
    name: 'Parameter Unit',
    selector: 'unit',
    sortable: true,
    right: true,
  },
  
  {
    name: 'Parameter Data',
    selector: 'results',
    sortable: true,
    right: true,
  
  }
  
];
class ACMFEvent extends React.Component{

    constructor(props) {
        super(props);
        this.state = {
          error: null,
          isLoaded: false,
          items: []
        };
      }
    
    componentDidMount() {
        axios.get('http://localhost:4000/acmfevent/')
        .then(response => {
            console.log(response.data);
            this.setState({ items: response.data });
        })
        .catch(function (error){
            console.log(error);
        })
    }

    render() {
        return (
         
            <DataTable
                title="ACMF Events"
                columns={columns}
                data={this.state.items.event}
                fixedHeader
                fixedHeaderScrollHeight="300px"
                highlightOnHover
                dense
            />
        )
      }
    };

export default ACMFEvent;